// BASIC
import React, { Component } from 'react'
import styled from 'styled-components'
// IMAGES
import logo from '../../Images/logo.svg'
import avatar from '../../Images/avatar.jpg'
import easyword from '../../Images/projects/easyword.png'
import easycalc from '../../Images/projects/easycalc.png'
import easymath from '../../Images/projects/easymath.png'
import cpp from '../../Images/languages/cpp.svg'
import css from '../../Images/languages/css.svg'
import excel from '../../Images/languages/excel.svg'
import html from '../../Images/languages/html.svg'
import js from '../../Images/languages/js.svg'
import python from '../../Images/languages/python.svg'
// ICONS
import {FaGrinWink, FaRegGrinWink} from 'react-icons/fa'

const AppComponent = styled.div`
	width: 100vw;
	height: 100vh;
	max-width: 100%;
	height: calc(var(--vh, 1vh) * 100);
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 30px;
`
const Logo = styled.img`
	width: 100%;
	max-width: 512px;
`
const Avatar = styled.div`
	min-width: 250px;
	min-height: 250px;
	background: url("${props => props.src}") no-repeat center center;
	background-size: cover;
	border-radius: 100%;
	margin: 10px;
	@media (min-width: 800px) {
		min-width: 300px;
		min-height: 300px;
	}
`
const Message = styled.div`
	text-align: center;
`
const MessageTitle = styled.h1`
	font-size: 40px;
	font-weight: bold;
	margin: 20px 0;
`
const MessageDescripition = styled.p`
	font-size: 20px;
`
const Wink = styled(FaGrinWink)`
	color: var(--color-main);
`
const Projects = styled.div`
	width: 100%;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
`
const BrowserLink = styled.a`
	border-radius: 100%;
	margin: 30px;
`
const Project = styled.img`
	width: 100px;
	height: 100px;
	border-radius: 100%;
	transition: all 0.3s ease;
	:hover {
		box-shadow: 0 0 20px var(--color-primary);
	}
`
const Skills = styled.article`
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
`
const Cards = styled.section`
	width: 100%;
	max-width: 1000px;
	display: flex;
	justify-content: space-around;
	flex-wrap: wrap;
`
const Card = styled.div`
	width: 150px;
	height: 200px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin: 20px;
	padding: 20px;
	border-radius: 10px;
	text-align: center;
	overflow: hidden;
	transition: all 0.3s ease;
	/* :nth-child(1) svg circle:nth-child(2) {
		stroke: #F16529;
	}
	:nth-child(2) svg circle:nth-child(2) {
		stroke: #2965f1;
	}
	:nth-child(3) svg circle:nth-child(2) {
		stroke: #F0DB4F;
	} */
	:hover {
		cursor: pointer;
		transform: translateY(-10px);
		box-shadow: 0 0 30px gray;
	}
`
const LangLogo = styled.img`
	display: block;
	width: 100%;
`
const Lang = styled.strong`
	color: var(--color-secondary);
	font-size: 25px;
	margin: 20px 0;
`
const SkillsDescription = styled.div`
	max-width: 1000px;
	margin: 30px 0;
	padding: 30px;
	font-size: 18px;
	text-align: center;
	border: 2px solid var(--color-main);
	border-radius: 20px;
`

class App extends Component {
	render() {
		return (
			<AppComponent>
				<Avatar src={avatar} />
				<Logo src={logo} />
				{/* <Message>
					<MessageTitle>Cześć!</MessageTitle>
					<MessageDescripition>Jestem Szulcuś, miło cię widzieć na moim portfolio <Wink /></MessageDescripition>
				</Message> */}
				<Projects>
					<BrowserLink href="https://easyword123.pl" target="_blank">
						<Project src={easyword} />
					</BrowserLink>
					<BrowserLink href="https://easycalc.web.app" target="_blank">
						<Project src={easycalc} />
					</BrowserLink>
					<BrowserLink href="https://easyvalue.web.app" target="_blank">
						<Project src={easymath} />
					</BrowserLink>
				</Projects>
				<Skills>
					<Cards>
						<Card>
							<LangLogo src={html} />
							<Lang>Html</Lang>
						</Card>
						<Card>
							<LangLogo src={css} />
							<Lang>CSS</Lang>
						</Card>
						<Card>
							<LangLogo src={js} />
							<Lang>JavaScript</Lang>
						</Card>
					</Cards>
					<SkillsDescription>
						Lorem, ipsum dolor sit amet consectetur adipisicing elit. Incidunt voluptate quas dicta accusamus aperiam quam dolores voluptatibus ea in, dolorem illo, soluta sed et magnam consequuntur officia, architecto itaque iste.
					</SkillsDescription>
					<Cards>
						<Card>
							<LangLogo src={python} />
							<Lang>Python</Lang>
						</Card>
						<Card>
							<LangLogo src={cpp} />
							<Lang>C++</Lang>
						</Card>
						<Card>
							<LangLogo src={excel} />
							<Lang>VBA</Lang>
						</Card>
					</Cards>
					<SkillsDescription>
						Lorem, ipsum dolor sit amet consectetur adipisicing elit. Incidunt voluptate quas dicta accusamus aperiam quam dolores voluptatibus ea in, dolorem illo, soluta sed et magnam consequuntur officia, architecto itaque iste.
					</SkillsDescription>
				</Skills>
			</AppComponent>
		);
	}
}

export default App